.input_form {
    border: 2px solid gray;
    margin-bottom: 15px;
    padding: 5px 20px;
    transition: 200ms linear;
    &:focus {
        border: 2px solid blue;
        -webkit-box-shadow: 4px 5px 38px 5px rgba(45, 113, 165, 0.69);
        -moz-box-shadow: 4px 5px 38px 5px rgba(45, 113, 165, 0.69);
        box-shadow: 4px 5px 38px 5px rgba(45, 113, 165, 0.69);
    }
}

.title_list {
    margin-bottom: 7px;
    color: gray;
    text-transform: uppercase;
    font-size: 23px;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
}

.label_form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    color: #000;
    font-size: 17px;
    font-weight: bold;
    font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}

.form_add_contact {
    margin: 0 auto;
    width: 40%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 20px;
    &:last-child {
        margin-bottom: 0;
    }
}

.submit_btn {
    transition: 200ms linear;
    margin: 0 auto;
    padding: 5px 15px;
    background-color: rgb(134, 129, 129);
    color: #000;
    text-transform: uppercase;
    border-radius: 15px;
    border: 2px solid #000;
    font-size: 16px;
    font-weight: normal;
    font-family: Arial, Helvetica, sans-serif;
    &:hover {
        border: 2px solid blue;
        -webkit-box-shadow: 4px 5px 38px 5px rgba(56, 59, 62, 0.29);
        -moz-box-shadow: 4px 5px 38px 5px rgba(56, 59, 62, 0.29);
        box-shadow: 4px 5px 38px 5px rgba(56, 59, 62, 0.29);
    }
}
